export var ENV = {
  Online: 'https://mapi.zjzwfw.gov.cn',
  Pre: '//pre-mapi.zjzwfw.gov.cn',
  Daily: '//47.96.150.251:8068'
};

var getHostByEnv = function (env, host) {
  /* 格式化host */
  if (host) {
    if (/\/$/.test(host)) {
      host = host.slice(0, -1);
    }

    return host;
  }
  /* daily & pre 仅供调试使用 */


  if ('daily' === env) {
    return ENV.Daily;
  } else if ('pre' === env) {
    return ENV.Pre;
  } else {
    return "//" + window.location.host;
  }
};

export default getHostByEnv;